.homepage {
  width: 100%;
  height: 100%;
  text-align: center;
}

.homepage-top {
  width: 100%;
  height: 20%;
}

.title {
  width: 100%;
  padding-top: 20%;
  font-size: 20px;
}

.homepage-bottom {
  width: 100%;
  height: 30%;
  font-size: 20px;
}

.name {
  font-size: 70px;
  font-family:'Playfair Display', serif;
}

.description {
  font-size: 20px;
}

@media only screen and (max-width: 420px) {
  .title {
    margin-top: 20%;
    font-size: 20px;
  }
  .description {
    font-size: 9.5px;
  }
  .bestQuote {
    font-size: small;
  }
}
